main{
  min-height: 80vh;
}
i{
  margin: 0.5rem;
}
h1{
  font-size: 1.8rem;
  padding: 1rem 0;
}
h2{
  font-size: 1.4rem;
  padding: 0.1rem;
}
button{
  margin: 1rem;
}
/* carousel*/
.carousel-item-next,
.carousel-item-prev,
.carousel-item.active{
  display: flex;
}
carousel-caption{
  position :absolute;
  top: 0;
}
carousel-caption h2{
  color: #fff;
}
.carousel img{
  height: 300px;
  padding: 30px;
  margin: 40px;
  border-radius: 50%;
}
.carousel a{
  margin: 0 auto;
}
